import { Component } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { MatTableDataSource } from '@angular/material/table';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { CountryDialogComponent } from './country-dialog/country-dialog.component';
import { MatSort } from '@angular/material/sort';
import { MatPaginator } from '@angular/material/paginator';
import { ViewChild } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  // properties
  countryDataSrc = new MatTableDataSource();
  columns = ["Name", "Continent", "SurfaceArea", "Population"];
  @ViewChild("sortCountry") sortCountry!: MatSort;
  @ViewChild("pageCountry") pageCountry!: MatPaginator;

  // ctor
  constructor(http: HttpClient, public dialog: MatDialog) {
    const URL = "http://ejd.songho.ca/syst24444/world.json";
    http.get(URL).subscribe({
      next: (json: any) => {
        this.countryDataSrc.data = json.Country;
        this.countryDataSrc.sort = this.sortCountry;
        this.countryDataSrc.paginator = this.pageCountry;
        console.log("Loaded JSON");
      },
      error: err => console.log(err),
    });
  }

  getFlagImage(country: any): any {
    let code = country.Code2.toLowerCase();

    return "http://ejd.songho.ca/syst24444/flags/" + code + ".jpg";
  }

  openDialog(country: any) {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.data = country;
    dialogConfig.width = '800px';
    this.dialog.open(CountryDialogComponent, dialogConfig);
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement)?.value;
    if (filterValue !== undefined) {
      this.countryDataSrc.filter = filterValue.trim().toLowerCase();
    }
  }  
}
